.storesPage {

  .selectArea {
    display: flex;
    align-items: center;
    @include rwd-custom(margin-top, 16px, 8px, 8px);
    @include rwd-custom(margin-bottom, 32px, 24px, 20px);
    .label {
      white-space: nowrap;
      @include rwd-custom(font-size, $fz-large, $fz-base, $fz-small);
      @include rwd-custom(margin-right, 20px, 20px ,12px);
      // @include mobile {
      //   flex-grow: 0;
      // }
    }
    select {
      border: 1px solid #CED4DA;
      @include rwd-custom(padding, 10px 20px, 12px 16px, 8px 16px);
      @include rwd-custom(width, 324px, 237px);
      @include rwd-custom(font-size, $fz-medium-2, $fz-base, $fz-small);
      @include mobile {
        flex-grow: 1;
      }
    }
  }

  .card {
    .infoList {
      margin-left: 36px;
      text-indent: -36px;
      li {
        + li {
          margin-top: 8px;
        }
      }
      .icon {
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 90% 90%;
        display: inline-block;
        width: 20px;
        height: 20px;
        vertical-align: middle;
        @include rwd-custom(margin-right, 12px, 16px);
      }
    }
  }

  &--detail {

    .card {
      @include pad {
        display: flex;
        align-items: center;
        > * {
          flex-grow: 1;
          flex-shrink: 1;
          flex-basis: 100%;
        }
        > :last-child {
          margin-left: $gutter;
        }
      }
      @include mobile {
        display: block;
        > :last-child {
          margin-left: 0;
        }
      }
    }
    
    .map {
      @include rwd-custom(margin-top, 0, 32px, 16px);
      iframe {
        width: 100%;
        @include rwd-custom(height, 100%, 465px, 227px);
      }
    }

  }
    
}